export default {
	data() {
		return {
			xy:false
		};
	},
	props:{
		name:String
	},
	components: {
	},
	created() {
		if(!uni.getStorageSync('xyfile')){
			this.xy=true
		}
	},
	methods:{
		sapprove(e){
			uni.setStorageSync('xyfile',e)
			this.xy=false
		},
		jump_xieyi(e){
			uni.navigateTo({
				url:'/pages/xieyi/xieyi?type='+e
			})
		}
	}
}
